import { PMData } from "../../../single/model/PMData";
import MassRow from "./MassRow";
import * as multiSelectUtil from '../../../../select/MultiSelectVMUtil';
import * as selectUtil from '../../../../select/SelectVMUtil';
import * as rrlf_lt from './location/RRLF_LocationType';
import * as mgiUtil from './rowgroup/MassGroupItemUtil'
import * as mgi_iUtil from './rowgroup/MGI_ItemUtil'
import * as mgii_ct from './rowgroup/MGI_I_CreateType'
import * as genUtil from '../../../../../util/generalUtil';
import { EntFilter } from "../../filter/EntFilter";
import MassGroupComponent from "./rowgroup/MassGroupComponent";
import * as fvt from '../../filter/FilterValueType'
import * as sft from '../../filter/StringFilterType'

export const pmData = () => {
  return PMData.Instance;
}

export const get = (q, dict) => {
  var qI = new MassRow();
  qI.Name = q;
  qI.Dict = dict;
  return qI;
}

export const GetRank = (q) => {
  return q.SupItem ? q.SupItem.Rank + 1 : 0;
}

export const GetStyle = (q) => {
  return {
    display: 'flex',
    marginLeft: q.Rank * 20
  }
}

export const GetIsHaveSub = (q) => {
  return q.SubItems.length !== 0;
}

export const GetToggleButtonContent = (q) => {
  return q.IsShowSub ? '-' : '+';
}

export const GetSubFieldSelectVM = (q) => {
  var storageList = pmData().MassItemFields;
  var obj = multiSelectUtil.get(storageList, undefined);
  obj.Placeholder = 'chn phn nhm'
  obj.OnSelectedItemChanged = (v) => q.TempSubFields = v;
  return obj;
}

export const Order = (list) => {
  if (list.length === 0) return list;

  var firstRow = list[0]
  var mgi = firstRow.OrderMGI;
  if (!mgi.IsHaveValueOrder) return list;

  var orderValues = mgi.OrderValues;
  return list.sort((a, b) => {
    return orderValues.indexOf(a.FullValue) - orderValues.indexOf(b.FullValue);
  });
}

export const GetTitle = (q) => {
  return q.Name;
}

export const GetLocationSelectVM = (q) => {
  var obj = selectUtil.get([], undefined);
  obj.Placeholder = 'chn v tr'
  obj.TitleRender = (item) => {
    var marginLeft = item.Rank * 15;
    return <div
      style={{ marginLeft }}>
      {item.Title}
    </div>
  }
  obj.OnSelectedItemChanged = (v) => {
    var factory = q.ReplaceFactory;
    factory.ReferenceRow = v;
  }
  return obj;
}

export const GetSubMassGroupItem = (q) => {
  var subItems = q.SubItems;
  if (subItems.length === 0) return undefined;
  return subItems[0].MassGroupItem;
}

export const GetSubFields = (q) => {
  var subMGI = q.SubMassGroupItem;
  if (!subMGI) return [];
  var mgis = subMGI.SupComponent.Items;

  var lastValidIndex = -1;
  var isValidHandle = true
  mgis.forEach((x, i) => {
    if (isValidHandle && x.HaveMassItemSubComponents.length === 0) {
      lastValidIndex = i;
    } else {
      isValidHandle = false;
    }
  })

  var currentIndex = mgis.indexOf(q.MassGroupItem);
  if (lastValidIndex === -1) return [subMGI.Field];
  var fields = mgis.filter((x, i) =>
    (currentIndex === -1 || currentIndex < i) &&
    i <= lastValidIndex)
    .map(x => x.Field);
  return fields;
}

export const CreateSubMassGroupItem = (q) => {
  var filter = new EntFilter();
  filter.ValueType = fvt.bystring;
  filter.StringFilterType = sft.byequal;
  filter.Values = [q.FullValue];

  var supMgi = q.MassGroupItem;

  var mgc = undefined;
  var mgcIndex = supMgi.SubComponents
    .findIndex(x => genUtil.IsSameValues(x.Filter.Values, filter.Values));

  if (mgcIndex !== -1){
    mgc = supMgi.SubComponents[mgcIndex]
  } else {
    mgc = new MassGroupComponent();
    mgc.SubItem = supMgi;
    mgc.Filter = filter;
    supMgi.SubComponents.push(mgc);
  }

  var mgi = mgiUtil.get(null, null, mgc);
  mgc.Items = [mgi];

  q.SubMassGroupItem = mgi;
  return mgi;
}

export const Add = (supRow, table) => {
  var mgi = supRow ?
    (supRow.SubMassGroupItem ? supRow.SubMassGroupItem :
      CreateSubMassGroupItem(supRow))
    : table.MainMGI;

  var mgi_I = mgi_iUtil.create(mgi, mgii_ct.byuser);
  mgi.Items.push(mgi_I);

  var row = get(mgi_I.Value, table);
  row.FullValue = mgi_I.Value;
  row.IsCreatedByUser = true;
  row.MassGroupItem = mgi;

  row.SupItem = supRow;
  row.CurrentList.push(row);
  return row;
}