import RebarMass_Dict from '../mass/rebar/RebarMass_Dict';
import * as phaseDictUtil from '../phase/Phase_DictUtil'
import * as util from './ProjectUtil'

export default class Project {
  phase_Dict
  get Phase_Dict(){
    if (!this.phase_Dict){
      this.phase_Dict = phaseDictUtil.get(this);
    }
    return this.phase_Dict;
  }

  rebarMass_Dict
  get RebarMass_Dict(){
    if (!this.rebarMass_Dict){
      this.rebarMass_Dict = util.GetRebarMass_Dict(this);
    }
    return this.rebarMass_Dict;
  }

  inputExportSet
  get InputExportSet(){
    if (!this.inputExportSet){
      util.GetInputExportSet_Async(this);
    }
    return this.inputExportSet;
  }
  set InputExportSet(v){
    this.inputExportSet = v;
    this.GetInputExportSetDone = true;
  }

  OnGetInputExportSetDone_Funcs = []

  getInputExportSetDone
  get GetInputExportSetDone(){
    return this.getInputExportSetDone;
  }
  set GetInputExportSetDone(v){
    this.getInputExportSetDone = v;
    this.OnGetInputExportSetDone_Funcs.forEach(x => x());
  }
}