import * as api from '../../../../../util/apiUtil'
import { PMData } from '../../../single/model/PMData';
import MassItem from './MassItem';
import * as elementUtil from '../../element/ElementUtil'
import * as mt from '../MassType'
import * as ut from '../../value/ValueUnit'

export const pmData = () => {
  return PMData.Instance;
}

export const get = (q, isGetElement = false) => {
  var qI = new MassItem();

  qI._id = q._id;
  qI.Value = q.value;
  qI.Unit = q.unit;
  qI.MassType = q.massType;
  qI.Info = q.info;
  qI.projectId = q.projectId;
  qI.workpackageId = q.workpackageId;
  qI.exportSetId = q.exportSetId;
  qI.exportSetVersionId = q.exportSetVersionId;
  qI.Element = q.Elements.length !== 0 ? q.Elements[0] : undefined;

  return qI;
}

export const GetDefaultRebarMass = (project) => {
  var qI = new MassItem();

  qI.Unit = ut.kg;
  qI.MassType = mt.rebar;
  qI.Info = {}
  qI.projectId = project._id;

  const setExportSetData = (exportSet) => {
    qI.exportSetId = exportSet._id;

    var currentVersion = exportSet.CurrentVersion;
    if (!currentVersion) {
      exportSet.OnGetCurrentVersionDone_Func.push(() => {
        currentVersion = exportSet.CurrentVersion;
        qI.exportSetVersionId = currentVersion._id;
      })
    } else {
      qI.exportSetVersionId = currentVersion._id;
    }
  }

  var inputExportSet = project.InputExportSet;
  if (!inputExportSet) {
    project.OnGetInputExportSetDone_Funcs
      .push(() => {
        inputExportSet = project.InputExportSet;
        setExportSetData(inputExportSet);
      })
  } else {
    setExportSetData(inputExportSet);
  }

  return qI;
}

export const GetProject = (q) => {
  var list = pmData().Project_Dict.Items;
  var { projectId } = q;
  var index = list.findIndex(x => x._id === projectId);
  return index !== -1 ? list[index] : undefined;
}

export const GetWorkpackage = (q) => {
  var list = pmData().Workpackage_Dict.Workpackages;
  var { workpackageId } = q;
  var index = list.findIndex(x => x._id === workpackageId);
  return index !== -1 ? list[index] : undefined;
}

export const GetPhases = (q) => {
  var list = [];
  var phases = pmData().Phase_Dict.Items;
  var exportSetVersionId = q.exportSetVersionId;
  phases.forEach(x => {
    if (x.exportSetVersionIds.includes(exportSetVersionId)) {
      list.push(x);
    }
  })
  return list;
}

export const GetValidPhasesByInput = (q) => {
  var inputPhases = pmData().MassResultComp.Phases;
  var phases = q.Phases;
  var list = [];
  phases.forEach(x => {
    if (inputPhases.includes(x)) {
      list.push(x);
    }
  })
  return list;
}

export const GetElementAsync = (q) => {
  var elemId = q.elementId
  if (elemId) {
    elementUtil.GetById(elemId, (data) => {
      q.Element = data
      q.IsGetElement = true;
      q.GetElementDone = true;
    });
  } else {
    q.IsGetElement = true;
  }
}

export const getAll = (apiData, handle) => {
  var url = "https://api.bimdev.vn/api/hbmr/newlist";

  var start = window.performance.now();

  api.post(url, apiData,undefined, 2000000)
    .then(response => response.data)
    .then(data => {
      var end = window.performance.now();
      console.log(`Execution time: ${(end - start)/1000} s`);

      if (!data) {
        console.log('mass result undefined');
        return;
      }

      var items = data.map(x => { 
        return get(x)
      })
      if (handle) {
        handle(items);
      }
    });
}

export const getRebars = (projectId, handle) => {
  if (!projectId) return;
  var url = `https://api.bimdev.vn/api/hbmr/rebar?projectId=${projectId}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var items = undefined;
      if (!data) {
        items = [];
      } else {
        var items = data.map(x => get(x))
      }
      if (handle) {
        handle(items);
      }
    });
}

export const getByViewDetail = (_id, handle) => {
  var url = `https://api.bimdev.vn/api/hbdv/massdetail?id=${_id}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var { viewDetail, masses } = data;
      var isGetElement = mt.IsGetElement(viewDetail.massType);
      var items = masses.map(x => get(x, isGetElement));
      if (handle) {
        handle(items);
      }
    });
}